import GameStage from "@/stages/GameStage"
import ChatMessage from "@/ChatMessage"
import Requirement from "@/Requirement"
import Deck from "@/cards/Deck"
import DialogueNode from "@/DialogueNode"
import GameButton from "@/GameButton"
import DialogueTree from "@/DialogueTree"

/** A linear segment of a dialogue tree, consisting of one or several nodes. */
export default class DialogueSegment {
  private nodes: ChatMessage[]
  private currentNodeIndex: number = -1

  public readonly id: string
  /** If this node is selectable, title may be used to represent it on a button. */
  public readonly title: string

  public requirements: Requirement[][] = []
  private optionIds: string[] = []
  private optionSegments: DialogueSegment[] = []
  private endpointId: string = ""
  public endpointLabel: string = "Next"

  constructor(messages: ChatMessage[], id: string, title: string = "", endpointId: string = "") {
    this.id = id
    this.title = title
    this.nodes = messages
    this.endpointId = endpointId

    if (this.nodes == undefined || this.nodes.length == 0) {
      this.nodes = [new ChatMessage()]
      this.start()
    }
  }

  /*copy(): DialogueSegment {
    let segment = new DialogueSegment(this.nodes, this.id, this.title, this.endpointId)
    segment.requirements = this.requirements
    segment.optionIds = this.optionIds
    segment.optionSegments = this.optionSegments
    segment.endpointLabel = this.endpointLabel

    segment.currentNodeIndex = this.currentNodeIndex
    return segment
  }*/

  currentMessage(): ChatMessage {
    let msg = this.nodes[this.currentNodeIndex]
    return msg || new ChatMessage()
  }

  start() {
    this.currentNodeIndex = 0
  }

  gotoNextNode() {
    if (this.currentNodeIndex < this.nodes.length - 1) this.currentNodeIndex += 1
  }

  isStarted(): boolean {
    return this.currentNodeIndex >= 0
  }

  isOnLastNode(): boolean {
    return this.currentNodeIndex == this.nodes.length - 1
  }

  hasEndpoint(): boolean {
    return this.endpointId != ""
  }

  getEndpoint(stage: GameStage): () => any {
    let endpoint = stage.endpoints[this.endpointId]
    if (endpoint != undefined) return endpoint
    else throw Error(`Could not select option "${this.id}" because endpoint ${this.endpointId} is undefined.`)
  }

  linkOptions(lookupTable: {[id: string]: DialogueSegment}) {
    this.optionSegments = []
    for (let optionId of this.optionIds) {
      if (lookupTable[optionId] == undefined) {
        console.error(`The "${this.id}" node has an unknown option "${optionId}". It has been skipped.`)
        continue
      }
      this.optionSegments.push(lookupTable[optionId])
    }
  }

  get options(): DialogueSegment[] {
    if (this.isOnLastNode() && this.hasEndpoint()) {
      let end = new DialogueSegment([], `${this.id}.end`, `${this.endpointLabel}`, this.endpointId)
      end.endpointLabel = this.endpointLabel
      return [end]
    } else if (this.isOnLastNode()) {
      return this.optionSegments
    }
    return [this]
  }

  getFulfilledOptions(deck: Deck, ignoreRequirements: boolean = false): DialogueSegment[] {
    return this.options.filter(segment => segment.isRequirementFulfilled(deck) || ignoreRequirements)
  }

  isRequirementFulfilled(deck: Deck): boolean {
    if (this.requirements.length == 0) return true

    for (let orgroup of this.requirements) {
      if (Requirement.orGroupFulfilled(deck, orgroup)) return true
    }
    return false
  }

  length(): number {
    return this.nodes.length
  }

  /**
   * Parses one json node to a dialogue segment.
   * @param state
   * @private
   */
  public static parseSegment(state: any): DialogueSegment {
    let id = state.id
    let title = (state.title) ? state.title : "Next"
    let messages = (state.text) ? ChatMessage.parseLines(state.text) : []
    let optionIds = (state.options) ? state.options : []

    let endpoint = (state.endpoint) ? state.endpoint : ""
    let endpointFun = (endpoint.fun) ? endpoint.fun : endpoint
    let endpointLabel = (endpoint.label) ? endpoint.label : "Next"

    let segment = new DialogueSegment(messages, id, title, endpointFun)
    segment.requirements = (state.requirements) ? Requirement.parseRequirements(state.requirements) : []
    segment.optionIds = optionIds
    segment.endpointLabel = endpointLabel

    return segment
  }

}
