import Requirement from "@/Requirement"
import Deck from "@/cards/Deck"
import Epilogue from "@/Epilogue"
import globalState from "@/store/globalState"
import util from "@/util"

export default class Epilogues {

  epilogues: Epilogue[] = []

  constructor() {
    for (let state of JSON.parse(globalState().endingsString.value)) {
      let ending = Epilogue.create(state)
      this.epilogues.push(ending)
    }
  }

  addEpilogue(epilogue: Epilogue) {
    this.epilogues.push(epilogue)
  }

  chooseEpilogues(deck: Deck): Epilogue[] {
    let fulfilled = []
    for (let epilogue of this.epilogues) {
      if (epilogue.isFulfilled(deck)) fulfilled.push(epilogue)
    }
    let maxLikelihood = fulfilled.reduce((prev, curr) => {
      if (curr.likelihood > prev.likelihood) return curr
      else return prev
    }).likelihood
    //fulfilled.sort((a, b) => b.score(deck) - a.score(deck))
    fulfilled = fulfilled.filter(ep => ep.likelihood == maxLikelihood)
    return fulfilled
  }

  selectEnding(id: string): Epilogue {
    for (let ending of this.epilogues) {
      if (ending.id == id) return ending
    }
    console.warn(`Could not select epilogue with id "${id}". Available ids: ${this.epilogues.map(e => e.id).join(", ")}`)
    return this.epilogues[0]
  }

  correctnessSimulation(iterations: number = 1000, log: (text: string) => void = console.log) {
    let endingsMap: { [id: string]: { fulfilled: number; chosen: number; score: number; appearedWith: string[] } } = {}
    for (let ending of this.epilogues) {
      endingsMap[ending.id] = { fulfilled: 0, chosen: 0, score: 0, appearedWith: [] }
    }

    // Simulate a bunch of hands
    for (let i = 0; i < iterations; i++) {
      let deck = Deck.referenceDeck().shuffle()
      let drawn = deck.draw(Math.round(6 + Math.random() * (4 * 3 + 8)))
      let picked = new Deck()

      // Add cards to the hand, if the drawn card is a parent, take a random subcard
      for (let card of drawn.cards) {
        if (card.isParent) {
          picked.addCard(util.pickFrom(card.subcards, Math.random()))
        } else {
          picked.addCard(card)
        }
      }

      if (iterations <= 5) {
        log(picked.toString())
      }

      // Keep stats on each ending
      for (let ending of this.epilogues) {
        if (ending.isFulfilled(picked)) {
          endingsMap[ending.id].fulfilled += 1
          endingsMap[ending.id].score += ending.score(picked)
        }
      }
      let chosenEps = this.chooseEpilogues(picked)
      for (let chosenEp of chosenEps) {
        endingsMap[chosenEp.id].chosen += 1
        // Keep track of which other endings this one appeared with
        endingsMap[chosenEp.id].appearedWith.push(...chosenEps.filter(ep => ep.id != chosenEp.id).map(ep => ep.id))
      }
    }

    log(`After simulating ${iterations} random hands, epilogues had the following stats:`)

    let sortedEndings = [...this.epilogues].sort((a, b) => endingsMap[b.id].chosen - endingsMap[a.id].chosen)
    for (let ending of sortedEndings) {
      let fulfilled = endingsMap[ending.id].fulfilled
      let chosen = endingsMap[ending.id].chosen
      let appearances: any = {}
      for (let epId of endingsMap[ending.id].appearedWith) {
        if (epId in appearances) appearances[epId] += 1
        else appearances[epId] = 1
      }
      log("-".repeat(80))
      log(
        `${ending.title.padEnd(12)} | ${chosen.toString().padStart(4)} available (${this.stringifyAppearances(appearances)}),
               ${fulfilled.toString().padStart(4)} fulfilled`
      )
    }
  }

  private stringifyAppearances(appearances: any): string {
    let s = ""
    let epIds = Object.keys(appearances)
    for (let [i, epilogueId] of epIds.entries()) {
      s += `${this.epilogues.find(e => e.id == epilogueId)?.title}: ${appearances[epilogueId]}`
      if (i + 1 < epIds.length) s += ", "
    }
    return s
  }

  testBimboEnding() {
    let ending = this.epilogues.filter(ending => ending.id == "bimbo")[0]
    let deck = new Deck()
    deck.addCard(Deck.referenceDeck().getCardById("intelligence.intelligence 3")!)
    deck.addCard(Deck.referenceDeck().getCardById("breast-size.breast size 3")!)
    //deck.addCard(Deck.referenceDeck().getCardById("intelligence.breast size intelligence 1")!)

    this.assert(ending.isFulfilled(deck))
  }

  private assert(value: boolean) {
    if (!value) console.error("Assertion failed")
  }

}
