import GameStage from "@/stages/GameStage"
import Game from "@/Game"
import TextStage from "@/stages/TextStage"
import Card from "@/cards/Card"
import Dice from "@/Dice"
import ValueMod from "@/effects/ValueMod"
import HighlightStage from "@/stages/HighlightStage"
import Deck from "@/cards/Deck"
import GameButton from "@/GameButton"
import util from "@/util"
import Epilogue from "@/Epilogue"
import Epilogues from "@/Epilogues"
import DialogueTree from "@/DialogueTree"

export default class EndStage extends GameStage {
  private epilogue: Epilogue | undefined
  private dialogue: DialogueTree | undefined

  constructor(game: Game) {
    super(game, "end")
    this.activeComponent = "HandView"

    this.endpoints.end = () => {
      this.end()
    }
  }

  onStart() {
    this.game.pickedCards.sortByFamily()
    this.game.menu.saveHand()

    this.createMessage(this.game.dialogue['endStage'])
    this.createYourNewLifeButton()
  }

  createYourNewLifeButton(overrideEnding: Epilogue | undefined = undefined) {
    let fulfilledEpilogues: Epilogue[] = this.game.menu.endings?.chooseEpilogues(this.game.player.hand) || []
    if (overrideEnding != undefined) {
      fulfilledEpilogues = []
      fulfilledEpilogues.push(overrideEnding)
    }

    this.buttons = []
    for (let ep of fulfilledEpilogues) {
      this.buttons.push(new GameButton(ep.title, () => {
        this.epilogue = ep
        this.dialogue = this.epilogue.dialogueTree
        if (overrideEnding != undefined) this.dialogue.ignoreRequirements = true
        this.dialogue.start(this.game.player.hand, this)
        this.game.title = this.epilogue.title
      }))
    }

    if (this.buttons.length == 0) {
      this.createMessage(`ERROR! No epilogues could be loaded. Please report the following to Gardamuse.`)
      this.createMessage(`DEBUG! Available epilogues: ${this.game.menu.endings?.epilogues.length}`)
      this.createMessage(`DEBUG! Fulfilled epilogues: ${fulfilledEpilogues}`)
      this.buttons.push(new GameButton("Back to Main Menu", () => {
        this.end()
      }))
    }
  }

  end() {
    let epilogueId = this.epilogue!.id
    let endingId = this.dialogue!.currentSegment.id
    endingId = endingId.substring(0, endingId.length - 4) // Remove ".end" from the segment id
    this.game.menu.unlockEnding(epilogueId, endingId)
    this.game.title = this.dialogue?.currentSegment.endpointLabel || ""
    this.buttons = [
      new GameButton("Save Screenshot", () => { util.screenshot('cards', `hand-${this.game.pickedCards.size()}`) }),
      new GameButton("Back to Menu", () => { this.game.end() })
    ]
  }
}
