import Requirement from "@/Requirement"
import Deck from "@/cards/Deck"
import DialogueTree from "@/DialogueTree"
import GameStage from "@/stages/GameStage"
import DialogueNode from "@/DialogueNode"
import ChatMessage from "@/ChatMessage"
import { list, object, serializable } from "serializr"

export default class Epilogue {
  public id: string = ""
  public title: string = ""
  public likelihood: number = 0
  private readonly requirements: Requirement[][] = []
  private nodes: any[] = [] // Dialogue nodes
  public mod: string = "" // A mod id if this epilogue is added by a mod, or an empty string if not.

  constructor(id: string, title: string, likelihood: number, requirements: Requirement[][], nodes: any[]) {
    this.id = id
    this.title = title
    this.likelihood = likelihood
    this.requirements = requirements
    this.nodes = nodes
  }

  static create(state: any) {
    let id = state.id
    let title = state.title
    let likelihood = (state.likelihood) ? state.likelihood : 0
    let requirements = Requirement.parseRequirements(state.requirements)
    let nodes = (state.nodes) ? state.nodes : []

    return new Epilogue(id, title, likelihood, requirements, nodes)
  }

  get dialogueTree(): DialogueTree {
    return DialogueTree.parseDialogue({ id: this.id, nodes: this.nodes })
  }

  isFulfilled(deck: Deck) {
    try {
      for (let ors of this.requirements) {
        let fulfilled = Requirement.orGroupFulfilled(deck, ors)
        if (fulfilled) return true
      }
    } catch (e) {
      console.error(`Could not calculate fulfilment for ending "${this.id}".\n${e.message}`)
    }
    return false
  }

  score(deck: Deck) {
    let sums = []
    try {
      for (let orGroup of this.requirements) {
        let sum = 0
        if (Requirement.orGroupFulfilled(deck, orGroup)) {
          for (let and of orGroup) {
            sum += and.score(deck)
          }
        }
        sums.push(sum)
      }
    } catch (e) {
      console.error(`Could not calculate score for ending "${this.id}".\n${e.message}`)
    }

    return Math.max(...sums) * this.likelihood
  }

  getNodes(): any[] {
    return this.nodes
  }

  getAllEndings(): string[] {
    let endings: string[] = []
    for (let node of this.nodes) {
      if ('endpoint' in node) {
        endings.push(node.id)
      }
    }
    return endings.sort((a, b) => this.getEndpointLabel(a).localeCompare(this.getEndpointLabel(b)))
  }

  findNodeParents(child: string): string[] {
    let parents: string[] = []
    for (let node of this.nodes) {
      if ('options' in node && node.options.includes(child)) {
        parents.push(node.id)
      }
    }
    return parents
  }

  /** Returns the list of node ids to select, in order, to get the specified ending. */
  getNodeChain(ending: string): string[] {
    let chain: string[] = [ending]
    while (true) {
      let parents = this.findNodeParents(chain[chain.length - 1])
      if (parents.length == 0) break
      chain.push(parents[0])
    }

    return chain.reverse()
  }

  getEnding(ending: string): ChatMessage[] {
    let nodeIds = this.getNodeChain(ending)
    let messages = []
    for (let nodeId of nodeIds) {
      let node = this.nodes.find(n => n.id == nodeId)
      messages.push(...ChatMessage.parseLines(node.text))
    }
    return messages
  }

  getEndpointLabel(ending: string): string {
    return this.nodes.find(n => n.id == ending).endpoint.label
  }

}
