import GameStage from "@/stages/GameStage"
import ChatMessage from "@/ChatMessage"
import DialogueNode from "@/DialogueNode"
import GameButton from "@/GameButton"
import util from "@/util"
import Requirement from "@/Requirement"
import Deck from "@/cards/Deck"
import DialogueSegment from "@/DialogueSegment"

export default class DialogueTree {

  private nodes: { [id: string]: DialogueSegment} = {}
  private activeNode: DialogueSegment | undefined
  private firstNode: DialogueSegment | undefined
  ignoreRequirements: boolean = false

  constructor(segments: { [id: string]: DialogueSegment}) {
    this.nodes = segments
  }

  /*copy(): DialogueTree {
    let tree = new DialogueTree(this.nodes)
    tree.activeNode = this.activeNode
    tree.firstNode = this.activeNode
    return tree
  }*/

  get currentSegment(): DialogueSegment {
    return this.activeNode || new DialogueSegment([new ChatMessage()], "", "", "")
  }

  get currentMessage(): ChatMessage {
    return (this.activeNode) ? this.activeNode.currentMessage() : new ChatMessage("", "Error: Dialogue tree not initialized.")
  }

  get options(): DialogueSegment[] {
    return (this.activeNode) ? this.activeNode.options : []
  }

  createOptionButtons(options: DialogueSegment[], deck: Deck, stage: GameStage): GameButton[] {
    let buttons: GameButton[] = []

    for (let option of options) {
      let title = (option.id != this.activeNode?.id) ? option.title : "Next"
      if (option.requirements.length > 0 && !option.isStarted()) {
        title = `__${title}__`
      }

      buttons.push(new GameButton(title, () => {
        this.select(option, deck, stage)
      }))
    }

    return buttons
  }

  start(deck: Deck, stage: GameStage) {
    if (this.firstNode != undefined) {
      this.select(this.firstNode, deck, stage)
    } else console.error(`Could not start dialogue tree as there was no first node.`)
  }

  select(segment: DialogueSegment, deck: Deck, stage: GameStage) {
    this.activeNode = segment

    if (!segment.isStarted()) {
      segment.start()
    } else if (!segment.isOnLastNode()) {
      segment.gotoNextNode()
    } else if (segment.isOnLastNode() && segment.hasEndpoint()) {
      segment.getEndpoint(stage)()
      return
    } else {
      console.error(`Segment ${segment.id} was expected to have an endpoint but does not.`)
    }

    stage.buttons = this.createOptionButtons(segment.getFulfilledOptions(deck, this.ignoreRequirements), deck, stage)
    stage.sendMessage(this.currentMessage)
  }

  /**
   * Creates a linear dialogue tree from a series of messages.
   * @param messages to use as nodes
   * @param endpointId name of the endpoint to call when done
   * @param endpointLabel label of the button that will call the endpoint
   */
  static createLinear(messages: ChatMessage[], endpointId: string, endpointLabel: string): DialogueTree {
    let id = util.uuid()

    let segment = new DialogueSegment(messages, id, "", endpointId)
    segment.endpointLabel = endpointLabel

    let tree = new DialogueTree({ [id]: segment })
    tree.firstNode = segment

    return tree
  }

  static parseDialogue(state: any): DialogueTree {
    // Create a map of all linear segments in this tree
    let segments: { [id: string]: DialogueSegment} = {}
    for (let node of state.nodes) {
      let linearNodes = DialogueSegment.parseSegment(node)
      segments[linearNodes.id] = linearNodes
    }

    // Create links between nodes with completely different ids
    for (let segment of Object.values(segments)) {
      segment.linkOptions(segments)
    }

    let tree = new DialogueTree(segments)

    // Set our active node as the first node in the parsed state
    tree.firstNode = segments[state.nodes[0].id]

    return tree
  }

}
