<template>
  <div class="settings-screen">
    <TabWindow :tabs="['mods', 'modCreation', 'tools', 'exit']" @close="$emit('close')">
      <template v-slot:mods>
        <div class="mods">
          <div>
            <h2>Mods</h2>
            <div class="button-row">
              <p style="margin: 0;">Click the button and then select a mod zip file to load it. It is recommended to back up your save file first. There may be no error message if something goes wrong.</p>
              <div>
                <label for="mod-import" class="load-button"><b>Load Mod</b></label>
                <input id="mod-import" type="file" accept=".zip" v-on:change="menu.loadMod"
                       style="display: none;">
              </div>
            </div>
            <p>Your save file may grow very large if you load many mods or mods with unoptimized images.</p>
            <p><b>Only load mods from trusted sources!</b></p>
          </div>
          <div>
            <h2>Active Mods</h2>
          </div>
          <BlushList style="overflow: auto;">
            <div v-for="mod in menu.mods" class="mod">
              <div class="mod-info">
                <h3 class="mod-title">{{ mod.name }}</h3>
                <p class="mod-version">v{{ mod.version }}</p>
                <p class="mod-authors">
                  by
                  <template v-for="(author, index) in mod.authors">
                    {{ author }}<span v-if="index + 1 < mod.authors.length">, </span>
                  </template>
                </p>
                <p class="mod-description">{{ mod.description }}</p>
                <p class="mod-cards">Cards: {{ mod.cards.length }}<span style="margin: 0 10px;"></span> Epilogues: {{ mod.epilogues.length }}</p>
                <p class="mod-epilogues"></p>
              </div>
              <TextButton class="delete-save-button" @click="removeMod(mod.id)">
                <h3>Remove mod</h3>
                <i>All mod content will be immediately removed from your save file.</i>
              </TextButton>
            </div>
          </BlushList>
        </div>
      </template>
      <template v-slot:modCreation>
        <h2>Create Your Own Mod!</h2>
        <p>If you feel there is some content missing from the game, you may be able to create a mod that adds it! Mods can currently:</p>
        <BlushList class="mod-supports">
          <p>Override existing cards.</p>
          <p>Add new cards.</p>
          <p>Add new epilogues.</p>
        </BlushList>
        <p>Please note that mod support is new in version 1.5. I have done as much testing as I can, but there may well be bugs.</p>
        <hr>
        <h2>How to Create a Mod</h2>
        <p>Mods are written in <a href="https://hjson.github.io/">Hjson</a>. Don't worry, Hjson files are just text and can be opened in any plain text editor such as Notepad. I can recommend <a href="https://atom.io/">Atom</a> or <a href="https://notepad-plus-plus.org/">Notepad++</a>.</p>
        <p><b>I have created an <a href="example-mod.zip">Example Mod</a></b> that you can download. Look at how that mod adds the type of content you want to do, and do it the same way. I recommend looking at the example mod before reading any more here.</p>
        <p>After opening the zip file you will see a folder, inside is a file called <code>info.hjson</code>. This file will contain information about your mod, such as its name. Inside the root folder are more folders, one for each type of moddable content, each with a specific name. You only need the folders you are adding content for.</p>
        <hr>
        <h2>Mod Distribution and Rules</h2>
        <p>I have not looked into mod distribution, but if you look into that, feel free to get in touch! I would quite like it to be as easy as possible to find existing mods.</p>
        <p>Make sure that your mod do not involve illegal content. Always be clear with what content your mod does contain.</p>
        <p>Paid mods are not allowed. Any mods you publish should be freely available.</p>
        <hr>

        <h2>Tags</h2>
        <p>Cards can have any number of tags. These tags are used by content filters, and to determine which ending the player fulfills. Tags on a parent card are inherited by all its subcards, but the subcards can also add its own tags.</p>
        <p>Currently, the following tags are in use:</p>
        <BlushList style="height: 50px;">
          <div style="display: flex; flex-wrap: wrap;">
            <p v-for="tag in allTags" style="margin: 0 5px;">"{{ tag }}"</p>
          </div>
        </BlushList>
        <p>The "extreme" tag is used to indicate that this card is an extreme version of one of its other tags.</p>
        <hr>

        <h2>Epilogue Requirements</h2>
        <p>Epilogues must specify requirements. If the player has cards that fulfill those requirements, the epilogue is considered fulfilled.</p>
        <p>An epilogue is not always show even if it is fulfilled. Epilogues have priority groups (0, 10, 20, 30, 40). Only fulfilled epilogues from the highest priority group that has any fulfilled epilogues will be available to the player.</p>
        <p>Requirements may contain card ids, card family ids, card tags and the special tag "all". These are compared to numbers and combined with "AND" and "OR" keywords. Let's look at some examples.</p>
        <div class="code-example">
          <p><b>Ex 1:</b> An epilogue with the following requirement will be fulfilled if the player has at least 1 level of a card with the iq tag, and at least level 4 of the gold card.</p>
          <code>iq > 0 AND gold > 3</code>
        </div>
        <br>
        <div class="code-example">
          <p><b>Ex 2:</b> The numbers are always card levels. Many cards only have one level, and count as 1, but leveled cards count as however much their level is. The following can be fulfilled by 3 different cards with the "iq" tag, or by for instance, the Level 5 Intelligence card.</p>
          <code>iq > 3</code>
        </div>
        <br>
        <div class="code-example">
          <p><b>Ex 3:</b> "OR" separates groups of "AND". The following is fulfilled if there is either at least 1 level of iq tag cards and at least level 4 of the gold card, OR if there is no more than 4 pet cards, at least 2 spell cards and no more than 1 lactation cards.</p>
          <code>iq > 0 AND gold > 3 OR pet &lt;= 4 AND spell >= 2 AND lactation &lt; 2</code>
          <p>It is NOT possible to use parenthesis to change the order of operations. OR always separates groups of AND.</p>
        </div>

        <p>Finally, each dialogue option in the epilogue may also have a requirement. These work in the same way as for epilogues. There should always be at least one dialogue option with no requirement.</p>
        <hr>

        <h2>Epilogue Likelihood (Priority Group)</h2>
        <div>
          <p>Each epilogue has the field <code>likelihood</code>. The higher the number in this field, the higher the priority group the epilogue belongs to.</p>
          <p>The player is able to choose between all fulfilled epilogues in the highest priority group that has any fulfilled epilogues.</p>
          <p>For instance, the "Unchanged" epilogue has <code>likelihood: 0</code>, "Bimbo" has <code>likelihood: 20</code> and "Goth" has <code>likelihood: 20</code> too. If the player fulfill all those epilogues' requirements, they will get to choose between "Bimbo" and "Goth".</p>
          <p>This means that endings that are in some sense "worse" can take priority over milder endings. So that if the player fulfills both "Bimbo" and "Bimbo Ditz", they will only be able to choose "Bimbo Ditz".</p>
          <p>Below are the priority groups for all built-in and currently loaded modded epilogues.</p>
        </div>
        <BlushList class="card-ids" style="min-height: 100px; height: auto;">
          <div v-for="ep in [...menu.endings.epilogues].sort((a, b) => a.likelihood - b.likelihood)"
               style="
               display: grid;
               grid-template-columns: 1fr 3fr;
               border-bottom: 1px dashed #fffa;"
               :style="{'background-color': `hsl(${ep.likelihood * 6}, 50%, ${util.clamp(10 + (ep.likelihood / 70) * 50, 0, 50)}%)`}"
          >
            <h3>{{ ep.title }}</h3> <p style="margin: 0;"><code style="background-color: #0000;">likelihood: {{ ep.likelihood }}</code></p>
          </div>
        </BlushList>
        <hr>

        <h2>Card Data</h2>
        <p>This list shows all card ids, as well as the tags for different cards. This includes cards in currently loaded mods. You'll want to refer to this list if you're looking to override an existing card.</p>
        <BlushList class="card-ids">
          <div v-for="card in refDeck.cards.sort((a, b) => a.name.localeCompare(b.name))" class="card-entry">
            <div class="label">
              <h3>{{ card.name }}</h3>
            </div>
            <div class="card-data">
              <p style="margin: 0;">Tags:</p>
              <div class="tags">
                <code v-for="tag in card.tags">"{{ tag }}"</code>
              </div>
              <p style="margin: 0;">Ids:</p>
              <code>"{{ card.id }}"</code>
              <template v-if="card.isParent">
                <template v-for="subcard in card.subcards" v-bind:key="subcard.id">
                  <code>"{{ subcard.id }}"</code>
                </template>
              </template>
            </div>
          </div>
        </BlushList>
        <hr>

        <h2>Extra Details</h2>
        <p>Mod version must be specified using valid <a href="https://semver.org/">Semantic Versioning</a>.</p>
        <p>It is not possible to override only a subcard. In that case you must override the entire card and all its levels.</p>
        <p>If you encounter bugs when loading your mod, you can try opening your browser console. There could be some useful warning messages there.</p>
      </template>
      <template v-slot:tools>
        <h2>Epilogue Simulation</h2>
        <p>When adding an epilogue it may be difficult to know how strict to make the requirements. This tool lets you simulate a number of games where the player picks cards at random.</p>
          <p>The tool shows how many times each epilogue was fulfilled and how many times the epilogue was available for the user to pick at the end screen. You can also see how many times another epilogue was available at the same time.</p>
        <p>The page may become unresponsive for a few seconds while simulating.</p>
        <div style="display: grid; grid-template-columns: 1fr 1fr;">
          <TextButton class="load-button" @click="simulateEpilogues(100)">Simulate 100 games</TextButton>
          <TextButton class="load-button" @click="simulateEpilogues(1000)">Simulate 1000 games</TextButton>
        </div>
        <pre id="epilogue-simulation-output"></pre>
        <hr>
        <h2>Epilogue Preview</h2>
        <p>Here you can go directly to the epilogues of any currently loaded mods with devmode activated.</p>
        <p>You will automatically fulfill the requirements for all dialogue options in this mode.</p>
        <p>Completing endings here will add them to the collection of your current save file.</p>
        <div class="epilogue-preview-buttons">
          <template v-for="epilogue of moddedEpilogues" v-bind:key="epilogue.id">
            <TextButton class="load-button" @click="toEnd(epilogue.id)">{{ epilogue.title }}</TextButton>
          </template>
        </div>
      </template>
    </TabWindow>
