<template>
  <div class="settings-screen">
    <TabWindow :tabs="['contentFilters', 'banCards', 'saves', 'exit']" @close="$emit('close')">
      <template v-slot:contentFilters>
        <FilterWindow :menu="menu" v-on:close="$emit('close')"></FilterWindow>
      </template>
      <template v-slot:banCards>
        <BanWindow :menu="menu" v-on:close="$emit('close')"></BanWindow>
      </template>
      <template v-slot:saves>
        <h2>Manual Saves</h2>
        <div class="button-row">
          <p style="margin: 0;">Running the game in incognito mode, or just want to back up your save? Download your current save game here.</p>
          <TextButton class="normal-button" @click="menu.exportSave()">
            <b>Download Save</b>
          </TextButton>
        </div>
        <div class="button-row">
          <p style="margin: 0;">Restore your save game from a previously downloaded save file. This will overwrite your current save file.</p>
          <div>
            <label for="save-import" class="load-button"><b>Restore Save</b></label>
            <input id="save-import" type="file" accept=".dd" v-on:change="menu.importSave"
                   style="display: none;">
          </div>
        </div>
        <h2 style="margin-top: 40px;">Delete Saves</h2>
        <div class="button-row">
          <p style="margin: 0;">Do you want to start your collection from scratch? Press this button to clear your save and start again.</p>
          <TextButton class="delete-save-button" @click="deleteCollection()">
            <b>Clear Collection</b>
          </TextButton>
        </div>
      </template>
    </TabWindow>
  </div>
</template>

<script lang="ts">
import { Options, Vue } from 'vue-class-component'
import util from "@/util"
import PatreonButton from "@/components/PatreonButton.vue"
import TextButton from "@/components/TextButton.vue"
import Menu from "@/Menu"
import Credit from "@/components/Credit.vue"
import FilterWindow from "@/components/FilterWindow.vue"
import BlushWindow from "@/components/BlushWindow.vue"
import TabWindow from "@/components/TabWindow.vue"
import BlushList from "@/components/BlushList.vue"
import BanWindow from "@/components/BanWindow.vue"

@Options({
  components: {
    BlushList,
    TextButton,
    Credit,
    FilterWindow,
    BanWindow,
    BlushWindow,
    TabWindow
  },
  props: {
    menu: Object
  },
  emits: ['close'],
  methods: {
    log(msg: any) {
      console.log(msg)
    },
    window() {
      return window
    },
    deleteCollection() {
      if (confirm("Are you sure you wish to delete your card collection? This can't be undone.")) {
        this.menu.deleteCollection()
      }
    }
  },
  computed: {
    util() {
      return util
    }
  }
})
export default class SettingsScreen extends Vue {
  menu!: Menu
}
</script>

