<template>
  <div class="tab-window">
    <div class="tabs">
      <template v-for="tab in tabs" v-bind:key="tab">
        <TextButton :class="{'active-tab': tab === activeTab}" @click="activateTab(tab)">
          <h3 class="tab-label">{{ util.camelToTitleCase(tab) }}</h3>
        </TextButton>
      </template>
    </div>
    <div class="content-window">
      <div class="content">
        <slot :name="activeTab"></slot>
      </div>
    </div>
  </div>
</template>

<script lang="ts">
import { Options, Vue } from 'vue-class-component'
import TextButton from '@/components/TextButton.vue'
import PlayingCard from "@/components/PlayingCard.vue"
import util from "@/util"

@Options({
  components: {
    TextButton, PlayingCard
  },
  props: {
    tabs: Array
  },
  emits: ['close', 'activatedTab'],
  methods: {
    activateTab(tab: string) {
      this.activeTab = tab
      this.$emit('activatedTab', tab)
      if (tab == 'exit' || tab == 'close') this.$emit('close')
    }
  },
  computed: {
    util() {
      return util
    }
  },
  created() {
    this.activateTab(this.tabs[0])
  }
})
export default class TabWindow extends Vue {
  tabs: string[] = []
  activeTab: string = ""
}
</script>

