<template>
  <div class="ban-window">
    <div>
      <h2>Ban Individual Cards</h2>
      <h3 v-if="!menu.isFilteringTooMuch()">Currently including {{ menu.cardsFilteredString() }}</h3>
      <h3 v-else>
        Currently including {{ menu.cardsFilteredString() }}. WARNING: There may not be enough cards left.
      </h3>
      <p>If the filters don't remove just that one card you don't want you can choose to ban any specific card, irregardless of your filter settings.</p>
      <p><b>Tip:</b> Only ban cards you really dislike. Ban too much and you'll ruin your own fun!</p>
    </div>
    <BlushList class="filter-entries">
      <div v-for="card in refDeck.cards.sort((a, b) => a.name.localeCompare(b.name))" class="filter-entry">
        <div class="label">
          <h3>{{ card.name }}</h3>
        </div>
        <template v-if="!card.isParent">
          <TextButton style=""
                      :class="{'selected-green': !isBanned(card.id) && !isFiltered(card.id),
                               'selected-yellow': isFiltered(card.id),
                               'selected-red': isBanned(card.id)}"
                      @click="toggleBan(card.id)" >
            <p v-if="isBanned(card.id)"> Banned </p>
            <p v-else-if="isFiltered(card.id)"> Filtered </p>
            <p v-else> Allowed </p>
          </TextButton>
        </template>
        <template v-else>
          <template v-for="subcard in card.subcards" v-bind:key="subcard.id">
            <TextButton style=""
                        :class="{'selected-green': !isBanned(subcard.id) && !isFiltered(subcard.id),
                               'selected-yellow': isFiltered(subcard.id),
                               'selected-red': isBanned(subcard.id)}"
                        @click="toggleBan(subcard.id)" >
              <p v-if="isBanned(subcard.id)"> Banned </p>
              <p v-else-if="isFiltered(subcard.id)" style="text-decoration: line-through;"> {{ subcard.name }} </p>
              <p v-else> {{ subcard.name }} </p>
            </TextButton>
          </template>
        </template>
      </div>
    </BlushList>
  </div>
</template>

<script lang="ts">
import { Options, Vue } from 'vue-class-component'
import TextButton from '@/components/TextButton.vue'
import util from "@/util"
import BlushWindow from "@/components/BlushWindow.vue"
import BlushList from "@/components/BlushList.vue"
import Menu from "@/Menu"
import FilterSetting from "@/FilterSetting"
import Deck from "@/cards/Deck"

@Options({
  components: {
    TextButton, BlushWindow, BlushList
  },
  props: {
    menu: Object,
    noCross: Boolean
  },
  emits: ['close'],
  methods: {
    isBanned(cardId: string): boolean {
      return this.menu.settings.isBanned(cardId)
    },
    isFiltered(cardId: string): boolean {
      return this.menu.settings.isFiltered(cardId)
    },
    toggleBan(cardId: string) {
      if (this.isBanned(cardId)) this.menu.settings.unbanCard(cardId)
      else this.menu.settings.banCard(cardId)
      this.menu.save()
      this.$forceUpdate()
    }
  },
  computed: {
    util() {
      return util
    }
  }
})
export default class BanWindow extends Vue {
  menu!: Menu
  noCross: boolean = false
  refDeck: Deck = Deck.referenceDeck()
}
</script>

