<template>
  <div class="filter-window">
    <div>
      <h2>Content Filters</h2>
      <h3 v-if="!menu.isFilteringTooMuch()">Currently including {{ menu.cardsFilteredString() }}</h3>
      <h3 v-else>
        Currently including {{ menu.cardsFilteredString() }}. WARNING: There may not be enough cards left.
      </h3>
      <p>The game generally isn't very extreme, but you may not like everything. Filters will try to remove most of the content you are not interested in.</p>
      <p><b>Tip:</b> Remove only what you actually dislike, not what makes for a "bad end", since those are often very fun.</p>
    </div>
    <BlushList class="instructions">
      <p><b>All</b> - Include all content in this category.</p>
      <p><b>Some</b> - Do not include extreme content in this category. Example: No immobilizingly large breast expansion.</p>
      <p><b>None</b> - Remove as much content in this category as possible.</p>
    </BlushList>
    <BlushList class="filter-entries">
      <div v-for="filter in menu.settings.filters" class="filter-entry">
        <div class="label">
          <h3>{{ filter.name }}</h3>
          <p>{{ filter.description }}</p>
        </div>
        <TextButton style="grid-area: b1;" :class="{'selected-green': filter.strictness === 0}" @click="setStrictness(filter, 0)"><h4>All</h4></TextButton>
        <TextButton style="grid-area: b2;" :class="{'selected-yellow': filter.strictness === 1}" @click="setStrictness(filter, 1)"><h4>Some</h4></TextButton>
        <TextButton style="grid-area: b3;" :class="{'selected-red': filter.strictness === 2}" @click="setStrictness(filter, 2)"><h4>None</h4></TextButton>
      </div>
    </BlushList>
  </div>
</template>

<script lang="ts">
import { Options, Vue } from 'vue-class-component'
import TextButton from '@/components/TextButton.vue'
import util from "@/util"
import BlushWindow from "@/components/BlushWindow.vue"
import BlushList from "@/components/BlushList.vue"
import Menu from "@/Menu"
import FilterSetting from "@/FilterSetting"

@Options({
  components: {
    TextButton, BlushWindow, BlushList
  },
  props: {
    menu: Object,
    noCross: Boolean
  },
  emits: ['close'],
  methods: {
    setStrictness(filter: FilterSetting, value: number) {
      filter.strictness = value
      this.menu.save()
    }
  },
  computed: {
    util() {
      return util
    }
  }
})
export default class FilterWindow extends Vue {
  menu!: Menu
  noCross: boolean = false
}
</script>

