import Menu from "@/Menu"
import Epilogues from "@/Epilogues"
import Epilogue from "@/Epilogue"
import ChatMessage from "@/ChatMessage"

export default class EndingsCollection {
  private menu: Menu
  private epilogues: Epilogues
  public currentEpilogue: Epilogue | undefined
  public currentEnding: string = ""

  constructor(menu: Menu) {
    this.menu = menu
    this.epilogues = (menu.endings) ? menu.endings : new Epilogues()
  }

  epiloguesList() {
    return this.epilogues.epilogues.sort((a, b) => a.title.localeCompare(b.title))
  }

  select(epilogueId: string, endingId: string) {
    this.currentEpilogue = this.epilogues.selectEnding(epilogueId)
    this.currentEnding = endingId
  }

  currentEndingMessages(): ChatMessage[] {
    if (this.currentEpilogue == undefined) return []
    return this.currentEpilogue.getEnding(this.currentEnding)
  }

  currentEndingLabel(): string {
    if (this.currentEpilogue == undefined) return ""
    return this.currentEpilogue.getEndpointLabel(this.currentEnding)
  }

  parseMessages(text: string): ChatMessage[] {
    if (text == undefined) return []

    return ChatMessage.parseLines(text)
  }

  unselect() {
    this.currentEpilogue = undefined
  }

  unlockedEpilogues(): Epilogue[] {
    return this.epiloguesList().filter((ep: Epilogue) => this.menu.isEpilogueUnlocked(ep.id))
  }

  unlockedEndings(epilogue: Epilogue): string[] {
    return epilogue.getAllEndings().filter(en => this.menu.isEndingUnlocked(epilogue.id, en))
  }

  nrOfAllUnlockedEndings(): number {
    let sum = 0
    for (let epilogue of this.epiloguesList()) {
      sum += this.unlockedEndings(epilogue).length
    }
    return sum
  }

  nrOfAllEndings(): number {
    let sum = 0
    for (let epilogue of this.epiloguesList()) {
      sum += epilogue.getAllEndings().length
    }
    return sum
  }
}
