import Game from "@/Game"
import globalState from "@/store/globalState"
import { reactive, toRaw } from "vue"
import Deck from "@/cards/Deck"
import Cheatmode from "@/Cheatmode"
import SaveSystem from "@/SaveSystem"
import { deserialize, serialize } from "serializr"
import util from "@/util"
import FilterSettings from "@/FilterSettings"
import Epilogues from "@/Epilogues"
import DrawStage from "@/stages/DrawStage"
import Save from "@/Save"
import Card from "@/cards/Card"
import Mod from "@/Mod"

export default class Menu {
  public static readonly GAME_VERSION = '1.5.3'

  private state: any = reactive({
    game: undefined,
    activeComponent: "StartMenu"
  })
  private cheatmode: Cheatmode | undefined
  private saveSystem: SaveSystem = new SaveSystem()
  public collection: any = reactive(new Deck([]))
  public settings: any = reactive(new FilterSettings())
  public unlockedEndings: {[epilogue: string]: {[ending: string]: boolean}} = {} // A key for each epilogue, each with an object where keys are ending ids
  public endings: Epilogues | undefined
  public mods: {[id: string]: Mod} = {}

  constructor() {
    globalState().load().then(() => {
      this.endings = new Epilogues()

      let save: Save | undefined = this.saveSystem.load("save-1")
      if (save != undefined) {
        this.useSave(save)
      }
      Deck.filters = this.settings.filters
      Deck.bannedCards = new Set(this.settings.bannedCards)

      if (this.endings == undefined || this.endings.epilogues.length < 1) {
        alert("ERROR! No endings could be loaded. Refresh this page to try again.\n\n" +
              "If this message keeps appearing, make sure you are connected to the internet, and try a different browser. " +
              "If that doesn't work, contact Gardamuse, or comment that you received this error multiple times," +
              "including what browser and device you are using.")
        console.log("Menu endings:", this.endings)
      }
    })
    this.cheatmode = new Cheatmode(this)
  }

  get game(): Game | undefined {
    return this.state.game
  }

  startGame() {
    this.state.game = new Game(this)
    this.activeComponent = this.game!.activeComponent
  }

  endGame() {
    this.saveHand()

    this.state.game = undefined
    this.activeComponent = "StartMenu"
  }

  saveHand() {
    this.collection.addDeck(this.state.game.player.hand)
    this.save()
  }

  buildSave(name: string): Save {
    let mods: Mod[] = []
    for (let id of Object.keys(this.mods)) {
      mods.push(this.mods[id])
    }

    return new Save(name, this.version, {
      collection: toRaw(this.collection.unique()).cards.map((card: Card) => ({ id: card.id })),
      settings: toRaw(this.settings),
      unlockedEndings: toRaw(this.unlockedEndings),
      mods: toRaw(mods)
    })
  }

  useSave(save: Save) {
    if (save.state.mods != undefined) {
      for (let modState of save.state.mods) {
        //@ts-ignore
        let mod: Mod = deserialize(Mod, modState)
        this.mountMod(mod)
        this.mods[modState.id] = mod
      }
    }

    this.collection = reactive(Deck.createFromCardStates(save.state.collection))
    for (let mod of Object.values(this.mods)) {
      if (mod.devmode) mod.cards.map(card => this.collection.addCard(card))
    }
    this.collection.sortByFamily()
    this.unlockedEndings = save.state.unlockedEndings || {}
    if (this.collection.size() != save.state.collection.length) {
      console.info(`Not all saved cards were loaded. Expected size ${save.state.collection.length}, actual size ${this.collection.size()}.`)
    }
    this.settings.load(save.state.settings)
  }

  save() {
    this.collection.sortByFamily()
    this.saveSystem.save(this.buildSave("save-1"))
  }

  exportSave() {
    this.save()
    util.download(`savegame.dd`, JSON.stringify(serialize(Save, this.buildSave("save-1"))))
  }

  importSave(event: any) {
    let file = event.target.files[0]
    let reader = new FileReader()
    reader.addEventListener('load', (e) => {
      // @ts-ignore
      let text: string = e.target.result as string
      //console.log("Importing save")
      let save: Save = SaveSystem.objectToSave(JSON.parse(text))
      this.useSave(save)
      this.save()
      console.log(`Imported save ${save.name}, version ${save.version}.`)
    })
    reader.readAsText(file)
  }

  loadMod(event: any) {
    let file = event.target.files[0]

    Mod.createFromZip(file).then(mod => {
      this.mods[mod.id] = mod
      Deck.resetStaticState()
      this.save()
      this.useSave(this.saveSystem.load("save-1")) // Reload save to apply mods to everything
    }).catch(e => {
      alert(`Mod loading failed with the following error.\n\n${e.message}`)
    })
  }

  unloadMod(modId: string) {
    delete this.mods[modId]
    this.save()

    Deck.resetStaticState()
    this.useSave(this.saveSystem.load("save-1")) // Reload save to apply mods to everything
  }

  mountMod(mod: Mod) {
    for (let card of mod.cards) {
      Deck.addReferenceCard(card)
    }
    for (let epilogue of mod.epilogues) {
      this.endings?.addEpilogue(epilogue)
    }
  }

  isEpilogueUnlocked(epilogue: string) {
    if (epilogue in this.unlockedEndings) return true
    return false
  }

  isEndingUnlocked(epilogue: string, ending: string): boolean {
    if (this.isEpilogueUnlocked(epilogue)) {
      let endings = this.unlockedEndings[epilogue]
      if (ending in endings) return true
    }
    return false
  }

  unlockEnding(epilogue: string, ending: string) {
    if (!this.isEpilogueUnlocked(epilogue)) this.unlockedEndings[epilogue] = {}
    this.unlockedEndings[epilogue][ending] = true
    this.save()
  }

  showCollection() {
    this.activeComponent = "CollectionScreen"
  }

  deleteCollection() {
    this.collection.removeAllCards()
    this.unlockedEndings = {}
    this.save()
  }

  showSettings() {
    this.activeComponent = "SettingsScreen"
  }

  showMods() {
    this.activeComponent = "ModScreen"
  }

  showEndingsCollection() {
    this.activeComponent = "EndingsCollectionWindow.vue"
  }

  cardsFilteredString(): string {
    return `${new Deck().createNew().size()} / ${Deck.referenceDeck().size()} cards and ${new Deck().createNew().unpack().size()} / ${Deck.referenceDeck().unpack().size()} levels.`
  }

  isFilteringTooMuch(): boolean {
    return new Deck().createNew().size() < Game.MAX_ROUNDS * DrawStage.DICE_SIZE * DrawStage.DICE_COUNT + Game.MAX_BONUS_ROLLS
  }

  get activeComponent(): string {
    if (this.game != undefined) return this.game.activeComponent
    return this.state.activeComponent
  }

  set activeComponent(value: string) {
    this.state.activeComponent = value
  }

  artCredits(): any[] {
    let artists: any = JSON.parse(globalState().artistsString.value)
    for (let artist of artists) {
      artist.cards = []
    }
    let deck = Deck.referenceDeck()
    for (let card of deck.allLevels) {
      // Find the artist of this card
      let artist = artists.find((a: any) => a.artist == card.artist)
      if (artist == undefined) continue

      if (card.isParent) {
        artist.cards.push(`${card.name}`)
      } else if (card.hasParent) {
        //artist.cards.push(`${card.parent.name}: ${card.name}`)
        artist.cards.push(`${card.parent!.name}`)
      } else {
        artist.cards.push(`${card.name}`)
      }
    }
    for (let artist of artists) {
      artist.cards = [...new Set(artist.cards)] // Remove duplicates
      artist.cards.sort()
    }
    return artists
  }

  patreonCredits() {
    let credits = globalState().patreonCredits.value
    return credits
  }

  get patchNotes() {
    return JSON.parse(globalState().patchNotesString.value)
  }

  get version(): string {
    return Menu.GAME_VERSION
  }
}
