import Deck from "@/cards/Deck"
import FilterSettings from "@/FilterSettings"
import FilterSetting from "@/FilterSetting"
import { serializable, serialize } from "serializr"

export default class Requirement {
  @serializable
  mode: string = "or" // or, and
  @serializable
  private args: string[] = []
  @serializable
  private operator: string = ">"
  @serializable
  private intensity: number = 0

  private fun: (deck: Deck, args: string[]) => number = Requirement.intensitySum

  constructor(mode: string, args: string[], operator: string, intensity: number, fun: (deck: Deck, args: string[]) => number = Requirement.intensitySum) {
    this.mode = mode
    this.args = args
    this.operator = operator
    this.intensity = intensity
    this.fun = fun
  }

  static functions(name: string): (deck: Deck, args: string[]) => number {
    let availableFunctions: { [key: string]: (deck: Deck, args: string[]) => number } = {
      sum: Requirement.intensitySum
    }
    return availableFunctions[name]
  }

  score(deck: Deck): number {
    //if (!this.isFulfilled(deck)) return -1
    if (this.args[0] == "all") return 0

    if (this.operator == "<") {
      return 0
    } else if (this.operator == "<=") {
      return 0
    } else if (this.operator == "==") {
      return 0
    } else if (this.operator == ">=") {
      return this.intensityScore(deck)
    } else if (this.operator == ">") {
      return this.intensityScore(deck) - 1
    }

    console.error(`Requirement operator "${this.operator}" was not recognized.`)
    return 0
  }

  isFulfilled(deck: Deck): boolean {
    if (this.operator == "<") {
      return this.intensityScore(deck) < this.intensity
    } else if (this.operator == "<=") {
      return this.intensityScore(deck) <= this.intensity
    } else if (this.operator == "==") {
      return this.intensityScore(deck) == this.intensity
    } else if (this.operator == ">=") {
      return this.intensityScore(deck) >= this.intensity
    } else if (this.operator == ">") {
      return this.intensityScore(deck) > this.intensity
    }
    console.error(`Requirement operator "${this.operator}" was not recognized.`)
    return false
  }

  intensityScore(deck: Deck): number {
    return this.fun(deck, this.args)
  }

  private static intensitySum(deck: Deck, idsOrTags: string[]): number {
    let sum = 0
    for (let idOrTag of idsOrTags) {
      if (Deck.referenceDeck().hasCard(idOrTag)) { // Id may be for card
        let card = deck.getCardById(idOrTag)

        if (card == undefined) {
          // Id may be for a parent card, in that case, count the highest available subcard
          card = deck.pack().getCardById(idOrTag)
          if (card != undefined) sum += card.subcards[card.subcards.length - 1].intensity

        } else {
          sum += Math.max(card.intensity, 1)
        }
      } else if (Deck.referenceDeck().onlyTag(idOrTag).size() > 0) { // Id may be a tag or family name
        for (let card of deck.onlyTag(idOrTag).cards) {
          sum += Math.max(card.intensity, 1)
          //console.log(`Adding Card ${card.fullTitle} level ${Math.max(card.level, 1)}`)
        }
      } else {
        throw new Error(`Bad requirement "${this.toString()}", "${idOrTag}" is neither an id, tag or family.`)
      }
    }
    return sum
  }

  /**
   * Checks if a single or group of requirements is fulfilled.
   * @param deck
   * @param orGroup
   */
  public static orGroupFulfilled(deck: Deck, orGroup: Requirement[]): boolean {
    let fulfilled = true
    for (let req of orGroup) {
      if (!req.isFulfilled(deck)) {
        fulfilled = false
        break
      }
    }
    return fulfilled
  }

  /**
   * Parses all requirements from a single requirements string.
   * @param reqs
   * @return orgroups A list of requirement-lists. Each requirement-list represents one OR-group.
   */
  public static parseRequirements(reqs: string): Requirement[][] {
    let requirements = []

    // Normalize: remove lowercase any newlines
    reqs = reqs.toLowerCase().replace(/(\r\n|\n|\r)/gm, " ")

    // Create list where each entry is an OR term. Each OR term is a list of AND terms. Each AND term is a string.
    let ors = reqs.split(/\bor\b/).map(or => or.split(/\band\b/))
    for (let ands of ors) {
      let mode = "or"
      for (let [i, exp] of ands.entries()) {
        if (i > 0) mode = "and"

        exp = exp.trim()
        let operator = this.getOperator(exp)
        let left = this.getLeft(exp, operator)
        let right = this.getRight(exp, operator)

        let [fun, args] = this.stripFun(left)
        args = args.map((arg: string) => arg.replaceAll("\"", ""))
        //console.log(`\t[${fun}]: [${args}] [${operator}] [${right}]`)
        requirements.push(new Requirement(mode, args, operator, Number.parseInt(right), Requirement.functions(fun)))
      }
    }

    return this.groupRequirements(requirements)
  }

  /**
   * Groups a list of requirements into OR groups.
   * @param reqs
   * @private
   */
  private static groupRequirements(reqs: Requirement[]): Requirement[][] {
    let requirements: Requirement[][] = []
    let group = []
    for (let req of reqs) {
      if (req.mode == "or" && group.length > 0) {
        requirements.push(group)
        group = []
      }
      group.push(req)
    }
    requirements.push(group)

    return requirements
  }

  /**
   * Finds the function and arguments of the left side of an expression
   * @param expression the left side, such as "iq" or "sum(iq, bimbo)"
   * @private
   */
  private static stripFun(expression: string): any[] {
    let funMatch = expression.match(/.*(?=\()/) // Everything before first parenthesis
    let fun
    if (funMatch != null) fun = funMatch[0] // Lookup function name
    else fun = ""

    let args: string[] = [expression]
    if (fun != "") {
      //let expressionMatch = expression.match(/(?<=\().*(?=\))/)
      let expressionMatch = expression.match(/\(.*(?=\))/)
      if (expressionMatch != null) {
        args = this.splitArgs(expressionMatch[0])
        args[0] = args[0].replace("(", "")
      } else console.error(`Bad expression: ${expression}`)
    }

    return [fun, args]
  }

  private static splitArgs(args: string): string[] {
    return args.split(",").map(arg => arg.trim())
  }

  private static getLeft(exp: string, operator: string): string {
    return exp.split(operator)[0]?.trim()
  }

  private static getRight(exp: string, operator: string): string {
    let e = exp.split(operator)[1]?.trim()
    return e || "0"
  }

  private static getOperator(exp: string): string {
    let operatorMatch = exp.match(/ > | >= | == | <= | < /)
    let operator
    if (operatorMatch == null) operator = ">="
    else operator = operatorMatch[0].trim()
    return operator
  }

  toString(): string {
    return `${this.mode.toUpperCase()} [${this.args}] [${this.operator}] [${this.intensity}]`
  }

  toJSON() {
    return serialize(this)
  }

}
