
import { Options, Vue } from 'vue-class-component'
import util from "@/util"
import TextButton from "@/components/TextButton.vue"
import Menu from "@/Menu"
import Credit from "@/components/Credit.vue"
import FilterWindow from "@/components/FilterWindow.vue"
import BlushWindow from "@/components/BlushWindow.vue"
import TabWindow from "@/components/TabWindow.vue"
import BlushList from "@/components/BlushList.vue"
import Deck from "@/cards/Deck"
import Epilogue from "@/Epilogue"
import EndStage from "@/stages/EndStage"
import Epilogues from "@/Epilogues"

@Options({
  components: {
    BlushList,
    TextButton,
    Credit,
    FilterWindow,
    BlushWindow,
    TabWindow
  },
  props: {
    menu: Object
  },
  emits: ['close'],
  methods: {
    log(msg: any) {
      console.log(msg)
    },
    window() {
      return window
    },
    deleteCollection() {
      if (confirm("Are you sure you wish to delete your card collection? This can't be undone.")) {
        this.menu.deleteCollection()
      }
    },
    removeMod(modId: string) {
      if (confirm("Unloading this mod will remove all its data from your save file. If you wish to update this mod, just load the new version instead.\n\nAre you sure you want to remove this mod?")) {
        this.menu.unloadMod(modId)
      }
    },
    simulateEpilogues(iterations: number) {
      let field = document.getElementById("epilogue-simulation-output")
      if (field == null) {
        console.error("Could not find log field.")
        return
      }
      field.innerText = ""
      let log = (s: string) => {
        field!.innerText += s + "\n"
      }
      this.menu.endings.correctnessSimulation(iterations, log)
    },
    toEnd(epilogueId: string) {
      this.menu.startGame()
      let stage = new EndStage(this.menu.game!) // Use a Dialoguestage instead?
      this.menu.game!.state.stage = stage
      stage.onStart()

      let endings: Epilogues = this.menu.endings || new Epilogues()
      let ending: Epilogue = endings.selectEnding(epilogueId)
      stage.createYourNewLifeButton(ending)
    }
  },
  computed: {
    util() {
      return util
    },
    refDeck() {
      return Deck.referenceDeck()
    },
    allTags() {
      let tags = new Set<string>()
      for (let card of Deck.referenceDeck().cards) {
        for (let tag of card.tags) {
          tags.add(tag)
        }
      }
      return [...tags.values()].sort()
    },
    moddedEpilogues(): Epilogue[] {
      let epilogues: Epilogue[] = []
      for (let ep of this.menu.endings.epilogues) {
        if (ep.mod == "") continue
        let modId = ep.mod
        let mod = this.menu.mods[modId]

        if (mod != undefined && mod.devmode) epilogues.push(ep)
      }
      return epilogues
    }
  }
})
export default class ModScreen extends Vue {
  menu!: Menu
}
