<template>
  <BlushWindow class="endings-collection-window" v-on:close="onClose()" :no-cross="endingsCollection.currentEpilogue === undefined">
    <template v-slot:title>
      <template v-if="endingsCollection.currentEpilogue === undefined">
        <h2 style="margin: 0;">Epilogues</h2>
      </template>
      <template v-else>
        <h2 style="margin: 0;">{{ endingsCollection.currentEndingLabel() }}</h2>
      </template>
    </template>
    <div class="blush-list" v-if="endingsCollection.currentEpilogue === undefined">
      <div v-for="epilogue in endingsCollection.unlockedEpilogues()" class="patch-window">
        <h2 style="grid-column: 1 / -1;">{{ epilogue.title }}
        <span class="text" style="font-size: 0.6em; font-weight: normal;">
          Unlocked endings: {{ endingsCollection.unlockedEndings(epilogue).length }} / {{ epilogue.getAllEndings().length }}
        </span>
        </h2>
        <template v-for="ending in endingsCollection.unlockedEndings(epilogue)" :key="ending">
          <TextButton class="ending-button" @click="endingsCollection.select(epilogue.id, ending)">
            <b>{{ epilogue.getEndpointLabel(ending) }}</b>
          </TextButton>
        </template>
      </div>
    </div>
    <template v-else>
      <ChatView :lines="endingsCollection.currentEndingMessages()"></ChatView>
    </template>
  </BlushWindow>
</template>

<script lang="ts">
import { Options, Vue } from 'vue-class-component'
import TextButton from "@/components/TextButton.vue"
import util from "@/util"
import Menu from "@/Menu"
import BlushWindow from "@/components/BlushWindow.vue"
import EndingsCollection from "@/EndingsCollection"
import ChatView from "@/components/ChatView.vue"
import Epilogue from "@/Epilogue"

@Options({
  components: {
    ChatView,
    TextButton,
    BlushWindow
  },
  props: {
    menu: Object,
    endingsCollection: Object
  },
  emits: ['close'],
  methods: {
    log(msg: any) {
      console.log(msg)
    },
    util() {
      return util
    },
    window() {
      return window
    },
    onClose() {
      if (this.endingsCollection.currentEpilogue == undefined) {
        this.$emit('close')
      } else {
        this.endingsCollection.unselect()
      }
    }
  }
})
export default class EndingsCollectionWindow extends Vue {
  menu!: Menu
  endingsCollection: EndingsCollection = new EndingsCollection(this.menu)
}
</script>

