import Hjson from "hjson"
import JSZip from "jszip"
import Deck from "@/cards/Deck"
import Card from "@/cards/Card"
import { list, object, primitive, serializable, serialize } from "serializr"
import semver from "semver/preload"
import Epilogue from "@/Epilogue"
import Menu from "@/Menu"

export default class Mod {
  @serializable
  public readonly id: string
  @serializable
  public readonly name: string
  @serializable
  public readonly version: string
  @serializable
  public readonly gameVersion: string
  @serializable(list(primitive()))
  public readonly authors: string[]
  @serializable
  public readonly description: string
  @serializable
  private readonly root: string
  @serializable
  public readonly devmode: boolean
  @serializable(list(object(Card)))
  public cards: Card[] = []
  @serializable(list(primitive()))
  private epilogueStrings: string[] = []

  private _epilogues: Epilogue[] = []

  constructor(id: string, name: string, version: string, gameVersion: string, authors: string[], description: string, root: string, devmode: boolean) {
    this.id = id
    this.version = version
    this.gameVersion = gameVersion
    this.name = name
    this.authors = authors
    this.description = description
    this.root = root
    this.devmode = devmode
  }

  get epilogues(): Epilogue[] {
    if (this._epilogues.length == this.epilogueStrings.length) return this._epilogues
    this._epilogues = []
    for (let s of this.epilogueStrings) {
      let ep = Epilogue.create(Hjson.parse(s))
      ep.id = `${this.id}.${ep.id}`
      ep.mod = this.id
      this._epilogues.push(ep)
    }
    return this._epilogues
  }

  static async createFromZip(f: File): Promise<Mod> {
    let jszip = new JSZip()

    let zip = await jszip.loadAsync(f)
    let root: string = Object.keys(zip.files)[0]

    // Map of mod relative paths to base64 images
    let images: {[path: string]: string} = {}

    let info = Hjson.parse(await zip.file(`${root}info.hjson`)?.async("string") || "")
    let familyCardGroups: any[] = []
    let epilogueStrings: string[] = []
    for (let path of Object.keys(zip.files)) {
      // Store all hjson files in the cards folder
      if (path.startsWith(`${root}cards/`) && path.endsWith(".hjson")) {
        let content: string = await zip.file(path)?.async("string") || ""
        let familyCardGroup: any = Hjson.parse(content)
        familyCardGroups.push(familyCardGroup)
      }
      // Store all images as base64 in a map
      if (path.startsWith(`${root}cards/`) && path.endsWith(".webp")) {
        let base64: string = await zip.file(path)?.async("base64") || ""
        images[path] = `data:image/png;base64,${base64}`
      }
      // Store all hjson files in the epilogues folder
      if (path.startsWith(`${root}epilogues/`) && path.endsWith(".hjson")) {
        let content: string = await zip.file(path)?.async("string") || ""
        epilogueStrings.push(content)
      }
    }

    // Create the mod by parsing all the cards
    let mod: Mod = Mod.create(info, root)
    mod.cards = mod.cards.concat(
      Deck.parseCards(
        familyCardGroups,
        (path: string) => {
          let base64url = images[`${root}cards/${path}`]
          return base64url || ""
        },
        info.id
      )
    )
    mod.epilogueStrings = epilogueStrings
    let _ = mod.epilogues // This forces parsing of all epilogues. We want to get errors now.

    return mod
  }

  private static create(info: any, root: string): Mod {
    let id = this.verifyId(info.id)
    let name = this.verifyName(info.name)
    let version = this.verifyVersion(info.version)
    let gameVersion = this.verifyGameVersion(info.gameVersion)
    let authors = this.verifyAuthors(info.authors)
    let description = this.verifyDescription(info.description)
    let devmode = this.verifyDevmode(info.devmode)

    return new Mod(id, name, version, gameVersion, authors, description, root, devmode)
  }

  private static verifyId(id: string): string {
    if (id == undefined || id.length == undefined || id.length < 4) {
      throw new Error(`Bad mod id "${id}".`)
    } else {
      return id
    }
  }

  private static verifyName(name: string): string {
    if (name == undefined || name.length == undefined || name.length < 1) {
      throw new Error(`Bad mod name "${name}".`)
    } else {
      return name
    }
  }

  private static verifyVersion(version: string): string {
    if (version == undefined) {
      throw new Error(`Mod must specify its version, but doesn't.`)
    } else if (semver.valid(version) == null) {
      throw new Error(`Mod version "${version}" is not a valid SemVer. Make sure that it follows the Semantic Versioning syntax.`)
    } else {
      return version
    }
  }

  private static verifyGameVersion(version: string): string {
    if (version == undefined) {
      throw new Error(`Mod must specify its latest compatible game version, but doesn't.`)
    } else if (semver.valid(version) == null) {
      throw new Error(`Mod game version "${version}" is not a valid SemVer. Make sure that it follows the Semantic Versioning syntax.`)
    } else if (semver.gt(version, Menu.GAME_VERSION)) {
      throw new Error(`Mod is for game version ${version}, but the current game version is only ${Menu.GAME_VERSION}`)
    } else {
      return version
    }
    return version
  }

  private static verifyAuthors(authors: string[]): string[] {
    if (authors == undefined || authors.length == undefined || authors.length < 1) {
      throw new Error(`Bad mod author info "${authors}".`)
    } else {
      return authors
    }
  }

  private static verifyDescription(description: string): string {
    if (description == undefined) {
      return ""
    } else {
      return description
    }
  }

  private static verifyDevmode(devmode: boolean): boolean {
    if (devmode == undefined) {
      return false
    } else {
      return devmode
    }
  }

  toJSON() {
    return serialize(this)
  }
}
