import { list, map, mapAsArray, object, primitive, serializable } from "serializr"
import FilterSetting from "@/FilterSetting"
import { reactive } from "vue"
import Deck from "@/cards/Deck"

export default class FilterSettings {
  @serializable(list(object(FilterSetting)))
  public filters: FilterSetting[]

  @serializable(list(primitive()))
  private bannedCards: string[] = []

  constructor() {
    this.filters = [
      new FilterSetting("expansion", "Expansion", "Changes in size to body parts."),
      new FilterSetting("iq", "Intelligence", "Changes that primarily affect intelligence."),
      new FilterSetting("doll", "Doll", "Changes towards a plastic, fake or doll-like appearance and behavior."),
      new FilterSetting("pet", "Pet", "Changes that enforces pet-like behavior, like having to stand on all fours, wear a leash etc."),
      new FilterSetting("animal", "Mythological", "Changes that give horns, ears, a tail etc. Note that you always remain primarily human."),
      new FilterSetting("lactation", "Lactation", "Changes that cause lactation."),
      new FilterSetting("preg", "Pregnancy", "Changes related to pregnancy."),
      new FilterSetting("reward", "Reward", "Don't care for rewards? Turn them off here."),
    ]

    this.filters.sort((a, b) => (a.name < b.name) ? -1 : 1)
  }

  load(state: any) {
    if (state == undefined) return

    for (let filter of state.filters) {
      let myfilter = this.filters.find(f => f.tag == filter.tag)
      if (myfilter == undefined) continue
      myfilter.load(filter)
    }

    if (state.bannedCards != undefined) {
      this.bannedCards = state.bannedCards
    }
  }

  public banCard(cardId: string) {
    this.bannedCards.push(cardId)
    Deck.bannedCards = new Set(this.bannedCards)
  }

  public unbanCard(cardId: string) {
    this.bannedCards = this.bannedCards.filter(id => id != cardId)
    Deck.bannedCards = new Set(this.bannedCards)
  }

  public isBanned(cardId: string): boolean {
    return this.bannedCards.find(id => id == cardId) != undefined
  }

  public isFiltered(cardId: string): boolean {
    let card = Deck.lookup(cardId)
    if (card == undefined) {
      console.warn(`Could not find ${cardId} while checking if it is filtered.`)
      return false
    }
    return card.isFiltered(this.filters)
  }
}
