
import { Options, Vue } from 'vue-class-component'
import TextButton from '@/components/TextButton.vue'
import util from "@/util"
import BlushWindow from "@/components/BlushWindow.vue"
import BlushList from "@/components/BlushList.vue"
import Menu from "@/Menu"
import FilterSetting from "@/FilterSetting"
import Deck from "@/cards/Deck"

@Options({
  components: {
    TextButton, BlushWindow, BlushList
  },
  props: {
    menu: Object,
    noCross: Boolean
  },
  emits: ['close'],
  methods: {
    isBanned(cardId: string): boolean {
      return this.menu.settings.isBanned(cardId)
    },
    isFiltered(cardId: string): boolean {
      return this.menu.settings.isFiltered(cardId)
    },
    toggleBan(cardId: string) {
      if (this.isBanned(cardId)) this.menu.settings.unbanCard(cardId)
      else this.menu.settings.banCard(cardId)
      this.menu.save()
      this.$forceUpdate()
    }
  },
  computed: {
    util() {
      return util
    }
  }
})
export default class BanWindow extends Vue {
  menu!: Menu
  noCross: boolean = false
  refDeck: Deck = Deck.referenceDeck()
}
