import Game from "@/Game"
import util from "@/util"
import ValueMod from "@/effects/ValueMod"

export default class Ability {
  private name: string = ""
  private cost: { value: number; resource: string } // resource: mana, bonus
  private fun: (game: Game) => any
  private available: (game: Game) => boolean
  private secret: (game: Game) => boolean
  private visible: (game: Game) => boolean

  constructor(name: string, cost: { value: number; resource: string },
    fun: (game: Game) => any = game => {},
    isAvailable: (game: Game) => boolean = game => true) {

    this.name = name
    this.cost = cost
    this.fun = fun
    this.available = isAvailable
    this.secret = game => false
    this.visible = game => true
  }

  setFun(fun: (game: Game) => any): Ability {
    this.fun = fun
    return this
  }

  setAvailableFun(isAvailable: (game: Game) => boolean): Ability {
    this.available = isAvailable
    return this
  }

  setSecretFun(isSecret: (game: Game) => boolean): Ability {
    this.secret = isSecret
    return this
  }

  setVisibleFun(isVisible: (game: Game) => boolean): Ability {
    this.visible = isVisible
    return this
  }

  activate(game: Game) {
    if (this.isAvailable(game)) {
      if (this.cost.resource == "mana") game.player.useMana(this.cost.value)
      if (this.cost.resource == "bonus") game.bonusRolls.addValue(-this.cost.value)

      this.fun(game)
    }
  }

  isAvailable(game: Game): boolean {
    return this.available(game) && (
      (this.cost.resource == "mana" && this.cost.value <= game.player.getMana()) ||
      (this.cost.resource == "bonus" && game.bonusRolls.value - this.cost.value <= game.bonusRolls.max)
    )
  }

  isVisible(game: Game): boolean {
    return this.visible(game) && (this.isAvailable(game) || (!this.isAvailable(game) && !this.secret(game)))
  }

  get title(): string {
    return `${this.name}`
  }

  get subtitle(): string {
    if (this.cost.value > 0) return `Pay ${this.cost.value} ${this.cost.resource}`
    else if (this.cost.value < 0) return `Gain ${-this.cost.value} ${this.cost.resource}`
    return ``
  }

  static buyMana(): Ability {
    let a = new Ability("+2 mana", { value: -1, resource: "bonus" },
      game => {
        game.player.addMana(2)
      },
      game => {
        return game.allowManaBuy()
      }
    ).setVisibleFun(game => {
      return game.allowManaBuy()
    })
    a.secret = game => true
    return a
  }

  static buyDice(): Ability {
    return new Ability("Buy dice", { value: 2, resource: "mana" },
      game => {
        game.diceCount += 1
      },
      game => {
        return game.allowDiceBuy()
      }
    )
  }

  static sellDice(): Ability {
    let a = new Ability("Sell dice", { value: -2, resource: "mana" },
      game => {
        game.diceCount -= 1
      },
      game => {
        return game.allowDiceSale()
      }
    )
    return a
  }

  static readonly PICK_ANY_CARD_COST: number = 7
  static pickAnyCard(cardPickFunction: (diceRolls: number[]) => any, deckSize: number, fixedCost: number = -1): Ability {
    let cost = Ability.PICK_ANY_CARD_COST + (deckSize - Ability.PICK_ANY_CARD_COST)
    cost = util.clamp(cost, Ability.PICK_ANY_CARD_COST, 12)
    if (fixedCost >= 0) cost = fixedCost

    let a = new Ability("Pick Any",
      { value: cost, resource: "mana" },
      game => {
        let highlights = []
        for (let i = 0; i < deckSize; i++) {
          highlights.push(i + 1)
        }
        cardPickFunction(highlights)
      }).setAvailableFun(
      game => {
        return game.diceReadyCount + game.diceRollingCount == 0 && !game.diceAreLocked()
      })

    return a
  }

  static addRound(): Ability {
    let a = new Ability("Add Round", { value: 7, resource: "mana" }).setFun(game => {
      game.round.maxMods[util.uuid()] = new ValueMod(1, "add")
    }).setAvailableFun(
      game => {
        return game.round.max < Game.MAX_ROUNDS
      }
    ).setVisibleFun(game => {
      return game.round.max < Game.MAX_ROUNDS
    })
    return a
  }

}
