import { list, primitive, serializable } from "serializr"
import Card from "@/cards/Card"
import globalState from "@/store/globalState"

export default class Family {
  @serializable
  public readonly id: string
  @serializable
  public readonly name: string
  @serializable
  public readonly thumb: string
  @serializable
  public readonly artist: string
  @serializable
  public readonly hue: string

  constructor(state: any) {
    this.id = state.id
    this.name = state.name
    this.thumb = state.thumb
    this.artist = state.artist ? state.artist : "Gardamuse"
    this.hue = state.hue
  }

  private static familiesLoaded = false
  private static _familyMap: {[key: string]: Family} = {}
  private static familyMap(): {[key: string]: Family} {
    if (this.familiesLoaded) return this._familyMap

    let families: any = JSON.parse(globalState().cardFamilyString.value)
    for (let state of families) {
      let family = new Family(state)
      this._familyMap[family.id] = family
    }
    this.familiesLoaded = true
    return this._familyMap
  }

  public static familyLookup(id: string): Family {
    let family: Family | undefined = Family.familyMap()[id]
    if (family != undefined) return family
    return new Family({ id: "none", name: "None", thumb: "", artist: "", hue: 0 })
  }
}
