<template>
  <div class="milkfarm" v-if="isLoaded">
    <template v-if="activeMenuTab === 'cards'">
      <CollectionWindow :deck="menu.collection"
                        :menu="menu"
                        groupByParent
                        v-on:activate-tab="activateTab"
                        v-on:activate-card="activateCard">
      </CollectionWindow>
    </template>
    <template v-else>
      <EndingsCollectionWindow :menu="menu" :endings-collection="endingsCollection"></EndingsCollectionWindow>
    </template>
    <TabWindow style="grid-area: s;" :tabs="['cards', 'endings', 'exit']" v-on:activatedTab="activateMenuTab">
      <template v-slot:cards>
      <div class="status-content">
        <div class="status-text">
          <template v-if="isInspectingCard">
            <p>These are all levels of the "{{ activeCard.name }}" card you've collected.</p>
            <p>To go back, click the current tab.</p>
            <p><b>Levels owned</b>:
              <span class="status-number">{{ activeCard.subcards.length }} / {{ activeCard.levels }}</span></p>
          </template>
          <template v-else>
            <p>These are all the cards you've collected.</p>
            <p>To inspect a card with multiple levels, click it.</p>
            <p><b>Owned</b>:
              <span class="status-number">
                {{ deck.size() }} / {{ refDeck.onlyTag(activeTab).size() }}</span> cards,
                <span class="status-number">
                  {{ deck.unpack().size() }} / {{ refDeck.onlyTag(activeTab).unpack().size() }}
                </span> levels
            </p>
          </template>
        </div>
        <ChatView class="transformation-chat" :lines="transformationText()"></ChatView>
        <div class="buttons">
          <TextButton @click="util.screenshot('cards', screenshotName())">Save Screenshot</TextButton>
        </div>
      </div>
      </template>
      <template v-slot:endings>
        <p>These are all the epilogues and endings you've found.</p>
        <p>Epilogues are unlocked by having a certain set of cards by the end of the game.</p>
        <p>Endings are unlocked by making dialogue choices in the epilogue. Some dialogue choices will only show up if you have a specific card.</p>
        <p>Click an ending to read it.</p>
        <br>
        <p><b>Unlocked epilogues</b>:
          <span style="font-size: 1.1em;">{{ endingsCollection.unlockedEpilogues().length }} / {{ endingsCollection.epiloguesList().length }}</span>
        </p>
        <p><b>Unlocked endings</b>:
          <span style="font-size: 1.1em;">{{ endingsCollection.nrOfAllUnlockedEndings() }} / {{ endingsCollection.nrOfAllEndings() }}</span>
        </p>
        <p></p>
      </template>
    </TabWindow>
  </div>
</template>

<script lang="ts">
import { Options, Vue } from 'vue-class-component'
import TextButton from '@/components/TextButton.vue'
import PlayingCard from "@/components/PlayingCard.vue"
import Card from '@/cards/Card'
import Menu from "@/Menu"
import util from "@/util"
import Deck from "@/cards/Deck"
import CollectionWindow from "@/components/CollectionWindow.vue"
import ChatView from "@/components/ChatView.vue"
import ChatMessage from "@/ChatMessage"
import TabWindow from "@/components/TabWindow.vue"
import EndingsCollectionWindow from "@/components/EndingsCollectionWindow.vue"
import EndingsCollection from "@/EndingsCollection"

@Options({
  components: {
    ChatView,
    TextButton,
    PlayingCard,
    TabWindow,
    CollectionWindow,
    EndingsCollectionWindow
  },
  props: {
    menu: Object
  },
  emits: ['close'],
  methods: {
    log(msg: any) {
      console.log(msg)
    },
    toggleScale(card: Card) {
      card.scale = !card.scale
      if (this.scaledCard == card) {
        this.scaledCard.scale = false
        this.scaledCard = new Card({})
      } else {
        this.scaledCard.scale = false
      }
      if (card.scale) this.scaledCard = card
    },
    activateTab(tab: string) {
      this.activeTab = tab
      this.activeCard = new Card({})
      this.activeSubCard = new Card({})
      console.log(`Active Tab: ${this.activeTab}`)
      console.log(this.refDeck.onlyTag(this.activeTab).cards.map((c: Card) => c.name).sort())
    },
    activateCard(card: Card) {
      if (card.isParent) {
        this.activeCard = card
        this.activeSubCard = new Card({})
      } else {
        this.activeSubCard = card
      }
    },
    activateMenuTab(tab: string) {
      this.activeMenuTab = tab
      if (tab == "exit") {
        this.$emit("close")
      }
    },
    transformationText() {
      return ChatMessage.parseLines(this.activeSubCard.transformation)
    },
    lookup(id: string): Card {
      return Deck.lookup(id)!
    },
    screenshotName(): string {
      if (this.isInspectingCard) {
        return `${util.toKebabCase(this.activeCard.name)}-${this.activeCard.subcards.length}-of-${this.activeCard.levels}`
      }
      return `${this.activeTab}-${this.deck.unpack().size()}-of-${this.refDeck.onlyTag(this.activeTab).unpack().size()}`
    }
  },
  computed: {
    tabs(): string[] {
      return [
        'all',
        'behavior',
        'body',
        'clothing',
        'event',
        'mind',
        'reward'
      ]
    },
    util() {
      return util
    },
    refDeck(): Deck {
      return Deck.referenceDeck().useFilters()
    },
    deck(): Deck {
      if (this.isInspectingCard) {
        return new Deck(this.activeCard.subcards).sortByLevel().useFilters()
      }
      return this.menu.collection.copy().onlyTag(this.activeTab).pack().unique().useFilters()
    },
    isInspectingCard(): boolean {
      return this.activeCard.id != undefined && this.activeCard.isParent
    }
  },
  async created() {
    //await globalState().load()
    this.isLoaded = true
  }
})
export default class CollectionScreen extends Vue {
  isLoaded = false
  menu!: Menu
  endingsCollection: EndingsCollection = new EndingsCollection(this.menu)
  scaledCard: Card = new Card({})
  activeTab: string = 'all'
  activeCard: Card = new Card({})
  activeSubCard: Card = new Card({})
  activeMenuTab: string = ""
}
</script>

