
import { Options, Vue } from 'vue-class-component'
import TextButton from "@/components/TextButton.vue"
import util from "@/util"
import Menu from "@/Menu"
import BlushWindow from "@/components/BlushWindow.vue"
import EndingsCollection from "@/EndingsCollection"
import ChatView from "@/components/ChatView.vue"
import Epilogue from "@/Epilogue"

@Options({
  components: {
    ChatView,
    TextButton,
    BlushWindow
  },
  props: {
    menu: Object,
    endingsCollection: Object
  },
  emits: ['close'],
  methods: {
    log(msg: any) {
      console.log(msg)
    },
    util() {
      return util
    },
    window() {
      return window
    },
    onClose() {
      if (this.endingsCollection.currentEpilogue == undefined) {
        this.$emit('close')
      } else {
        this.endingsCollection.unselect()
      }
    }
  }
})
export default class EndingsCollectionWindow extends Vue {
  menu!: Menu
  endingsCollection: EndingsCollection = new EndingsCollection(this.menu)
}
